@import "src/common/Styles/sass/variables";
@import "src/common/Styles/sass/mixins";

.contacts {
  background-color: $bgSecondColor;
  padding: 100px 0 100px;

  .contacts_information {
    h3 {
      font-size: 20px;
    }

    span {
      display: inline-block;
      padding-bottom: 15px;
    }

    i {
      color: $primaryColor;
      margin-right: 20px;
      font-size: 25px;
    }

    li {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
    }
  }

  .contacts_container {
    height: 25%;
    width: 80%;
    margin: 0 auto;

    display: flex;
    flex-direction: row;

    justify-content: space-around;
    align-items: center;
  }


  .block_from {
    .contacts_from {
      width: 500px;
      gap: 15px;

      display: flex;
      flex-direction: column;

      .blockForm__inputs {
        display: flex;
        justify-content: space-between;

        input {
          width: 230px;
          height: 45px;

          padding: 0.375rem 0.75rem;
          font-size: 1rem;
          line-height: 1.5;
          color: #495057;

          background-color: $bgColor;
          background-clip: padding-box;
          border: 1px solid #ced4da;

          &:focus {
            outline: 1px solid rgba(0, 123, 255, 0.4);
            box-shadow: 0 0 8px rgba(0, 123, 255, 0.9);
          }
        }
      }

      textarea {
        width: 100%;
        height: 180px;

        font-size: 1rem;
        padding: 0.375rem 0.75rem;
        line-height: 1.5;
        color: #495057;

        background-color: $bgColor;
        background-clip: padding-box;
        border: 1px solid #ced4da;
        resize: vertical;

        &:focus {
          outline: 1px solid rgba(0, 123, 255, 0.4);
          box-shadow: 0 0 8px rgba(0, 123, 255, 0.9);
        }
      }

      @media screen and (max-width: 625px) {
        height: 180px;
        width: 100%;
      }

      .submit_btn {
        @include btnStyle;

        padding: 0 20px;
        line-height: 40px;
      }
    }
  }
}


